import { _Link, Img } from './Parts';

export default class DataContainer {
  constructor(params) {
    this.videoLink = params.videoLink || '';
    this.relLink = params.relVideo || '';
    this.titleContent = params.titleContent || '';
    this.srcImg = params.srcImg || '';
    this.altImg = params.altImg || '';
    this.channelTitle = params.channelTitle || '';
    this.description = params.description || '';
    this.publishedAt = params.publishedAt || '';
    this.countViev = params.countViev || '';
  }

  getElement(YT) {
    // + Image
    const a = new _Link({ href: this.videoLink, target: '_blank', rel: this.relLink });
    const aElem = a.getElement(YT);
    aElem.innerText = this.titleContent;
    const divLinkWrapper = YT.createElement('div');
    divLinkWrapper.setAttribute('class', 'link');
    divLinkWrapper.appendChild(aElem);
    const img = new Img({ src: this.srcImg, alt: this.title });
    const divImgWrapper = YT.createElement('div');
    divImgWrapper.setAttribute('class', 'img');
    divImgWrapper.appendChild(divLinkWrapper);
    divImgWrapper.appendChild(img.getElement(YT));
    // - Image
    // + Data
    const divDataWrapperIco = Array(3);
    for (let index = 0; index < 3; index += 1) {
      divDataWrapperIco[index] = YT.createElement('div');
    }
    divDataWrapperIco[0].setAttribute('class', 'author_1');
    divDataWrapperIco[1].setAttribute('class', 'upload-date_1');
    divDataWrapperIco[2].setAttribute('class', 'view-count_1');
    divDataWrapperIco[0].innerText = this.channelTitle;
    divDataWrapperIco[1].innerText = this.publishedAt;
    divDataWrapperIco[2].innerText = this.countViev;
    const divDataWrapper = YT.createElement('div');
    divDataWrapper.setAttribute('class', 'data');
    for (let index = 0; index < 3; index += 1) {
      divDataWrapper.appendChild(divDataWrapperIco[index]);
    }
    const divDescription = YT.createElement('div');
    divDescription.setAttribute('class', 'description');
    divDescription.innerText = this.description;
    const divContainer = YT.createElement('div');
    divContainer.setAttribute('class', 'container');
    divContainer.appendChild(divImgWrapper);
    divContainer.appendChild(divDataWrapper);
    divContainer.appendChild(divDescription);
    const liContainer = YT.createElement('li');
    liContainer.appendChild(divContainer);
    return liContainer;
  }
}
