export class Img {
  constructor(params) {
    this.src = params.src || '';
    this.alt = params.alt || '';
  }

  getElement(YT) {
    const elem = YT.createElement('img');
    if (this.src) {
      elem.setAttribute('src', this.src);
    }
    if (this.alt) {
      elem.setAttribute('alt', this.alt);
    }
    return elem;
  }
}

export class Button {
  constructor(className = '', name = '', text) {
    this.className = className;
    this.name = name;
    this.text = text;
  }

  getElement(objDocument) {
    const elem = objDocument.createElement('button');
    if (this.className) {
      elem.setAttribute('class', this.className);
    }
    if (this.name) {
      elem.setAttribute('name', this.name);
    }
    elem.innerText = this.text;
    return elem;
  }
}
