import { Input, Label } from './Parts';
import App from '../Controllers/MainContent';

export default class Request {
  constructor(params) {
    const idName = params.idName || '';
    const className = params.className || '';
    const defMassage = params.defMassage || '';
    this.Input = new Input('search', idName, className, defMassage);
    let forLabel = idName;
    if (forLabel === '') {
      forLabel = className;
    }
    let labelIdName = '';
    if (idName) {
      labelIdName = `${idName}_l`;
    }
    let labelClassName = '';
    if (className) {
      labelClassName = `${className}_l`;
    }
    this.Label = new Label({ forElem: forLabel, idName: labelIdName, className: labelClassName });
  }

  addEventListener() {
    const elem = this.elemForListen;
    elem.addEventListener('keydown', (event) => {
      if (event.keyCode === 13) {
        if (elem.value) {
          const appAction = new App(elem.value);
          appAction.start();
        }
      }
      return '';
    });
  }

  getElement(YT) {
    const elem = this.Label.getElement(YT);
    this.elemForListen = this.Input.getElement(YT);
    elem.appendChild(this.elemForListen);
    return elem;
  }
}
